html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
}

body {
  background-color: #262626;
}
body :focus {
  outline: 2px dotted #bebfc5;
  outline-offset: 3px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.l-shell {
  z-index: 1;
  max-width: 75em;
  height: 100%;
  margin: 0 auto;
  padding: 0;
  background: #fff;
  border: solid #262626;
  border-width: 8px 0 2px 0;
  transition: 0.3s ease all;
  transform: translateX(0);
  transform: translate3d(0, 0, 0);
}

.content-well {
  padding: 0 0.8rem 3rem;
  background: #fff;
}
@media (min-width: 64rem) {
  .content-well {
    padding: 0 1.8rem 3rem;
  }
}
.content-well :focus {
  outline: 0;
}

body.is-pushed .l-shell {
  transform: translateY(236px);
  transform: translate3d(0, 236px, 0);
}

.l-site-nav {
  margin-top: 24px;
  padding: 16px;
}

